.page {
  font-family: $font-family;
  font-size: 16px;
  line-height: 150%;
  color: $main-text-color;

  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;
    background-color: $main-backgr-color;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    opacity: 0;
    transition: all $effects-duration;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}

.page:has(.page__menu:target) {
  overflow: hidden;
  height: 100vh;
}
